/* Styling topbar */
.top-nav-wrap > span{
  padding: 0 45px;
  border-left: 3px #fff solid;
  display: inline-block;
  text-decoration: none !important;
}

.top-nav-wrap {
  text-align: center;
  -webkit-transition: font-size 0.4s;
  transition: font-size 0.4s;
}

.option {
  position: relative;
  padding-bottom: 3px;
  line-height: normal;
  line-height: initial;  /* added */
  cursor: pointer;
}
.option:after {
  content: '';
  display: block;
  margin: auto;
  height: 3px;
  width: 0px;
  background: transparent;
  -webkit-transition: width .5s ease, background-color .5s ease;
  transition: width .5s ease, background-color .5s ease;
}
.option:hover:after {
  width: 100%;
  background: #ddd;
}

.first-option{
  border: none !important;
}

.first-2-options{
  border-left: none !important;
}

.about-margin{
  margin-bottom: 150px;
}

.spacer-about{
  padding-top: 100px;
}

.spacer-projects{
  padding-top: 70px;
}

.contact-nothing{
  margin-top: 350px;
  padding-top: 100px;
}

