/* Hidding bottom scrollbar, and making right scrollbar to always appear */

@media only screen and (max-width: 768px){
  .top-nav-wrap > span{
    border-left: none;
    display: inline-block;
    vertical-align: middle;
    width: 25%;
    font-size: 17px;
    padding: 1px;
  }
  .fourth-option:hover:after {
    width: 70% !important;
  }
}

@media only screen and (max-width: 576px) {
  /* Styling borders */
  .first-option:hover:after {
    width: 60% !important;
  }
  .second-option:hover:after {
    width: 85% !important;
  }
  .third-option:hover:after {
    width: 80% !important;
  }
  .fourth-option:hover:after {
    width: 70% !important;
  }
}

@media only screen and (min-width: 576px) and (max-width: 850px) {
  .top-nav-wrap > span{
    font-size: 18px;
  }
  /* Styling borders */
  .first-option:hover:after {
    width: 35% !important;
  }
  .second-option:hover:after {
    width: 95% !important;
  }
  .third-option:hover:after {
    width: 75% !important;
  }
}

