/* Making particles work in background */
.particles {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}



/* Positioning div to middle of screen and setting cursor to default */
.welcome-header{
  cursor: default;
  margin-top: 30vh;
}

.more-about{
  cursor: pointer;
  margin-top: 15vh;
}

/* Handling "bouncing" arrow animation */
.bounce{
  -webkit-animation: bounce 1.85s infinite;
          animation: bounce 1.85s infinite;
  margin-top: 6.5vh;
}
@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-2vw);
            transform: translateY(-2vw);
  }
  65% {
    -webkit-transform: translateY(-1.5vw);
            transform: translateY(-1.5vw);
  }
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-2vw);
            transform: translateY(-2vw);
  }
  65% {
    -webkit-transform: translateY(-1.5vw);
            transform: translateY(-1.5vw);
  }
}


/* Replacing blue color of link, with standard inthereted from parent */
