.about-content-wrapper{
  color: #ccc;
  padding-left: 200px;
  padding-right: 200px;
}

.more-header-container{
  margin-top: 65px;
  font-weight: 400;
}

.more-header{
  font-size: 43px;
  font-weight: 600;
}

.text-about{
  margin-top: 65px;
  font-size: 23px;
}

.show_features{
  margin-top: 100px;
  cursor: pointer;
}

.features-spacer{
  margin-top: 550px;
}

.features-spacer-down{
  margin: 70px;
}

.feature-container{
  padding: 0 0px;
  margin-top: 100px;
}

.feature-text{
  margin-top: 22px;
  margin-left: auto;
  font-size: 20px;
}

.feature-image{
  margin-top: 50px;
  float: left;
  margin-right: 70px;
}

.feature-read-more{
  margin-top: 30px;
  font-size: 23px;
  font-weight: 600;
  text-decoration: underline;
}

.scroll_to_top{
  position: absolute;
  top: 0;
  left: 0;
}

.feature-header{
  font-size: 35px;
  font-weight: 700;
}

.clear-both{
  clear: both;
}

.small{
  display: none;
}

.bounce{
  margin-bottom: 10px;
}


@media only screen and (max-width: 992px) {
  .small{
    display: block;
  }
  .large{
    display: none;
  }
  .about-content-wrapper{
    padding-left: 0px;
    padding-right: 0px;
  }
  .feature-header{
    font-size: 28px;
  }
  .feature-image{
    float: initial;
    margin-left: 60px;
    margin-top: 20px;
  }
  .more-header{
    font-size: 33px;
  }
  .text-about{
    font-size: 19px;
  }
}

@media only screen and (max-width: 576px) {
  .bounce-arrow{
    width: 11vw;
  }
}
@media only screen and (min-width: 576px) and (max-width: 768px) {
  .bounce-arrow{
    width: 8vw;
  }
}
@media only screen and (min-width: 768px) and (max-width: 992px) {
  .bounce-arrow{
    width: 5vw;
  }
}
