/* Div that wraps this component */
.component-welcome{
  color: #ccc;
  margin: 0;
}

canvas{
  width: 100vw;
  height: 100vh;
}

/* Incrasing weight of header */
.welcome-h1{
  font-weight: 600;
  font-size: 35px;
}

/* Making particles work in background */
.particles {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}



/* Positioning div to middle of screen and setting cursor to default */
.welcome-header{
  cursor: default;
  margin-top: 30vh;
}

.more-about{
  cursor: pointer;
  margin-top: 15vh;
}

/* Handling "bouncing" arrow animation */
.bounce{
  animation: bounce 1.85s infinite;
  margin-top: 6.5vh;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-2vw);
  }
  65% {
    transform: translateY(-1.5vw);
  }
}


/* Replacing blue color of link, with standard inthereted from parent */
a{
  color: inherit;
}
a:hover{
  color: inherit;
}

/* Making arrow take 3% of viewport width */
.bounce-arrow{
  width: 3vw;
}

@media only screen and (max-width: 576px) {
  .welcome-header{
    margin-top: 15vh;
  }
  .bounce-arrow{
    width: 11vw;
  }
}
@media only screen and (min-width: 576px) and (max-width: 768px) {
  .welcome-header{
    margin-top: 22vh;
  }
  .more-about{
    margin-top: 10vh;
  }
  .bounce-arrow{
    width: 8vw;
  }
}
@media only screen and (min-width: 768px) and (max-width: 992px) {
  .welcome-header{
    margin-top: 25vh;
  }
  .more-about{
    margin-top: 10vh;
  }
  .bounce-arrow{
    width: 5vw;
  }
}

/* Adding more maring between welcome and about components for large screens */
@media only screen and (min-width: 1600px) {
  .component-welcome{
    margin-bottom: 300px;
  }
}
